import React from "react";
import { Link } from "react-router-dom";
import "../styles/ProductItem2.css";

const ProductItem2 = ({ product }) => {
  const currentPoints = 3500; //   ()
  const remainingPoints = currentPoints - product.points;

  return (
    <div className="ProductItem">
      <div className="ProductHeader">{product.name}</div>
      <img className="ProductImage" src={product.image} alt={product.name} />
      <div className="ProductDetails">
        <p>
            <span>{currentPoints}P</span>
        </p>
        <p>
          (CU) <span>{product.points}P</span>
        </p>
        <p>
            <span>{remainingPoints}P</span>
        </p>
      </div>
      <p> ?</p>
      <Link to="/barcode">
        <button className="BuyButton"></button>
      </Link>
    </div>
  );
};

export default ProductItem2;
